@use 'abstracts';
@use 'base';
// @use 'components';
// @use 'layout';
// @use 'pages';
// @use 'themes';
@use 'vendors';
@use "sass:map";

:root {
  --bp-1: 900px;
}

$bp-1: 900px;

$days-of-week: (
  'monday': 'mon',
  'tuesday': 'tue',
  'wednesday': 'wed',
  'thursday': 'thu',
  'friday': 'fri',
  'saturday': 'sat',
  'sunday': 'sun',
);

$days-of-week-short: (
  'mon': 'monday',
  'tue': 'tuesday',
  'wed': 'wednesday',
  'thu': 'thursday',
  'fri': 'friday',
  'sat': 'saturday',
  'sun': 'sunday',
);

@function day-of-week($day) {
  $key: map.has-key($days-of-week-short, $day);
  @if $key {
    @return map.get($days-of-week-short, $day);
  } @else {
    @return map.get($days-of-week, $day);
  }
}

.landing-page {
  background: lightgrey;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 5px solid magenta;
}

.welcome,
.welcome-short {
  text-transform: lowercase;
}

.welcome {
  display: none;
}

.welcome-short {
  display: block;
  text-align: center;
}

@media (min-width: $bp-1) {
  .welcome {
    display: block;
  }

  .welcome-short {
    display: none;
  }
}

// layout
.month {
  display: grid;
  grid-template-columns: repeat(2, minmax(100px, 1fr));
  row-gap: 0.5em;
  text-align: center;
}

.month-name {
  grid-column: 1;
  font-weight: 900;
  letter-spacing: 1px;
  // text-transform: uppercase;
}

.month-name::after {
  content: '';
}

.month-year {
  grid-column: -2;
  font-weight: 900;
}

@media (min-width: $bp-1) {
  .month {
    grid-template-columns: repeat(7, minmax(50px, 1fr));
  }
  .month-name {
    grid-column: 2;
  }

  .month-year {
    grid-column: -4 / span 3;
  }
}

.day {
  width: 90%;
  grid-column: 1 / span 2;
  display: grid;
  grid-template-rows: 1.5em 1fr;
  row-gap: 0.35em;
  grid-template-columns: repeat(2, minmax(1em, 1fr));
  justify-self: center;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba($color: #000000, $alpha: 0.1);
  padding: 0.5em;
  transform: scale(1);
  transition: transform 250ms ease-out;
}

.day:hover,
.day:focus {
  transform: scale(1.05);
  background: black;
  color: white;
}

.day-name,
.day-date {
  line-height: 1;
}
.day-name {
  font-weight: 700;
}

.day-date {
  font-weight: 700;
}

@media (min-width: $bp-1) {
  .day {
    // grid-template-rows:
    //   1em
    //   1.5em
    //   repeat(3, max-content);
    // grid-template-columns: 1fr;
    grid-column: auto;
    display: flex;
    flex-direction: column;
    font-size: 1rem;
  }

  .day-date {
    grid-row: 2;
  }
}

.day-name-short {
  display: none;
}

.shift {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  text-align: center;
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba($color: #000000, $alpha: 0.1);
  padding: 0.5em;
  transform: scale(1);
  transition: transform 250ms ease-in-out;
}

.shift:hover,
.shift:focus {
  transform: scale(1.05);
  background: black;
  color: white;
}

.who {
  text-align: left;
  font-weight: 600;
}

.initials {
  display: none;
}

@media (min-width: $bp-1) {
  .shift {
    grid-template-columns: 1fr;
    font-size: 1rem;
  }

  .who {
    text-align: center;
  }
}

